form{
  margin-bottom: 20px;
}

span{
  display: block;
}

.Small_box{
  width: 250px;
  height: auto;
}

.Page{
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
}

.Wrapper_2{
  width: 1000px;
  height: auto;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  background-color: lightblue;
}

.Wrapper{
  width: 1000px;
  height: auto;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
}

.Header{
  display: flex;
  justify-content: space-between;
  width: 900px;
  margin-top: 30px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
